export class EventCon {

    private static instance: EventCon;
    public static get ins(): EventCon {
        if (!this.instance) {
            this.instance = new EventCon();
        }
        return this.instance;
    }

    //  GUI
    public gold;

    //  GUI
    public key;

    constructor(){
        this.gold = new BABYLON.Observable();
        this.key = new BABYLON.Observable();
    }
}